<template>
	<view>
		<view class="All">
			<view class="content" v-for="(item,index) in navList" :key="index" >
				<view :class="{'active':isActive === index}" @click="checked(index)">
					{{item.title}}
				</view>
			</view>
		</view>
		<view class="content_bobao" v-if="isActive==0">
			<view class="">
				<button type="default" @click="getCurrentLocation()" class="qq"></button>
				<button type="default" @click="getaddress()"></button>
				<view class="">
					{{positionInfo.address}}
				</view>
			</view>
		</view>
		<view class="content_concern" v-if="isActive==1">
			1
		</view>
		<view class="content_time" v-if="isActive==2">
			2
		</view>
		<view class="content_wait_one" v-if="isActive==3">
			<view class="page-body">
				<view class="page-section page-section-gap">
					<map style="width: 100%; height: 300px;" :latitude="latitude" :longitude="longitude" :markers="covers">
					
					</map>
				</view>
				<view class="text">
					<uni-table>
						
					</uni-table>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isActive: 0,
				navList:[
					{
						index: 0,
						title: '疫情播报',
						
					},{
						index: 1,
						title: "出行政策"
					},{
						index: 2,
						title: "检测机构"
					},{
						index: 3,
						title: "风险地区"
					}
				],
				scrollTop: 0,
				id:0,
				title: 'map',
				latitude: 34.72468,
				longitude: 113.6401,
				covers: [{
					latitude: 34.72468,
					longitude: 113.6401,
				}, {
					latitude: 34.72468,
					longitude: 113.6401,
				}],
				positionInfo: {
					address: '',
					longitude: '', //经度
					latitude: '', //纬度
				}
			}
		},
		methods: {
			checked(index) {
				this.isActive = index
			},
			getCurrentLocation() {
				var that = this //在uniapp中药定义一下this才能使用
				uni.getLocation({
					type: 'wgs84',
					success: function(res) {
						console.log(res)
						that.positionInfo.longitude = res.longitude;
						that.positionInfo.latitude = res.latitude;
						that.loAcquire(that.positionInfo.longitude,         
						that.positionInfo.latitude)
					}
				});
			},
			// 获取当前地址
			loAcquire(longitude, latitude) {
				var that = this;
				uni.showLoading({
					title: '加载中',
					mask: true
				});
				var str = `output=jsonp&key='5MTBZ-ZBRRU-AGCV5-BXUUJ-Z5DUV-5OBOQ'=${latitude},${longitude}` 
				this.$jsonp('https://apis.map.qq.com/ws/geocoder/v1/?' + str, {}).then(res => {
					console.log(res);
					uni.hideLoading();
					if (res.status == 0) {
						that.positionInfo.address = '当前位置是:' + res.result.address_component.street_number; //当前定位
					}
				})
			},
			getaddress() {
				var that = this
				uni.chooseLocation({
					success: function(res) {
						that.positionInfo.address = '选择的位置是：' + res.name
					}
				});
			},
		},
	}
</script>

<style>
	.All{
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 3%;
	}
	.All  .content {
		height: 90rpx;
		line-height: 90rpx;
		width: 100%;
		text-align: center;
		font-size: 32rpx;
		font-family: Alibaba PuHuiTi;
		color: #333;
	}
	.active {
		position: relative;
		color: #1F75FE;
	}
	.active::after {
		content: "";
		position: absolute;
		width: 100rpx;
		height: 4rpx;
		background-color: #1F75FE;
		left: 0px;
		right: 0px;
		bottom: 0px;
		margin: auto;
	}
	.content_bobao{
		display: flex;
		flex-direction: column;
		width: 100%;
		justify-content: flex-start;
	}
	.qq{
		
		width: 10%;
		height: 30px;
	}
</style>
